/* Customising Nav */
.navbar {
  background-color: #f0f0f0;
}

.navbar-light .navbar-nav .nav-link {
  color: #1c3e59 !important;
}

/* Buttons */
button,
input[type="submit"],
.button {
  background: #468fc9;
  color: #ffffff;
  border-radius: 0 !important;
}

.form-control,
.form-control-lg,
.input-group {
  border-radius: 0 !important;
}

/* Headings for all pages */
.jumbotron {
  background-image: url(/rank-finder/static/media/rank-finder-blue-gradient-background.20ca25cc.jpg);
  background-position: center;
  color: #f0f0f0;
}
.jumbotron h1 {
  color: #ffffff;
}
.jumbotron h3::before {
  content: "| ";
}
.jumbotron h3 {
  color: #f0f0f0 !important;
  text-indent: 0px;
}
.body-background {
  background: #ffffff;
  margin-bottom: -50px;
}

/* News Styles */
.custom-news {
  padding-top: 50px;
}
.card {
  border: none !important;
  box-shadow: 16px 10px 15px -6px rgba(153, 153, 153, 0.47);
  height: 350px;
  -webkit-transition: -webkit-transform 0.05s;
  transition: -webkit-transform 0.05s;
  transition: transform 0.05s;
  transition: transform 0.05s, -webkit-transform 0.05s;
}
.card a {
  color: #f0f0f0 !important;
}
.card a:hover {
  background-position: right center;
  color: #ffffff;
  text-decoration: none;
}
.card-title {
  font-weight: bold;
  padding: 10px 5px;
}

.card-text {
  padding: 0px 15px;
}
.card:hover {
  -webkit-transform: scale(1.025);
          transform: scale(1.025);
  color: #000;
  color: initial;
}
.card-body {
  padding-bottom: 0.25rem !important;
}
.card-footer {
  border-top: none !important;
}
.card-footer-top {
  background: rgba(28, 62, 89, 0) !important;
}
.card-footer-top small {
  padding: 0px 15px;
  color: #1c3e59 !important;
  font-weight: bold;
}
.card-footer-bottom {
  background-image: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(70, 143, 201, 1)),
    to(rgba(93, 171, 234, 0.9920343137254902))
  ) !important;
  background-image: -webkit-linear-gradient(
    left,
    rgba(70, 143, 201, 1) 0%,
    rgba(93, 171, 234, 0.9920343137254902) 100%
  ) !important;
  background-image: linear-gradient(
    to right,
    rgba(70, 143, 201, 1) 0%,
    rgba(93, 171, 234, 0.9920343137254902) 100%
  ) !important;
  text-align: center !important;
}
.card-footer-bottom:hover {
  background-position: right center !important;
  color: #ffffff !important;
  cursor: pointer;
}

