/*
Primary-color: #1c3e59 - Dark
Secondary-color: #468fc9 
Background-color: #f0f0f0
Extra-Color: #dda222 - rgb(221, 162, 34) - Yellow
*/

/* JQuery scroll effect */
.bg-dar {
  background: transparent !important;
}
.bg-dar.scrolled {
  background: #f0f0f0 !important;
  -webkit-animation-name: fade;
          animation-name: fade;
  -webkit-animation-duration: 2.5s;
          animation-duration: 2.5s; /* Originally 2.5s */
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}
@-webkit-keyframes fade {
  from {
    background: transparent !important;
  }
  to {
    background: #f0f0f0 !important;
  }
}
@keyframes fade {
  from {
    background: transparent !important;
  }
  to {
    background: #f0f0f0 !important;
  }
}

/* Navbar Styles */
nav {
  -webkit-transition: top 0.3s;
  transition: top 0.3s; /* Transition effect when sliding down (and up) */
}

#mainNav {
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
  background-color: #fff;
  -webkit-transition: background-color 0.2s ease;
  transition: background-color 0.2s ease;
}

#mainNav .navbar-brand {
  /* background: #f0f0f0; */
  color: #212529;
}

#mainNav .navbar-nav .nav-item .nav-link {
  color: #ffffff;
  font-weight: 700;
  font-size: 0.9rem;
  padding: 0.75rem 0;
}

#mainNav .navbar-nav .nav-item .nav-link:hover,
#mainNav .navbar-nav .nav-item .nav-link:active {
  color: #f4623a;
}

#mainNav .navbar-nav .nav-item .nav-link.active {
  color: #f4623a !important;
}
/* End Navbar Styles */

/* Hero styles */
header.masthead {
  padding-top: 10rem;
  padding-bottom: calc(10rem - 72px);
  background: url(/rank-finder/static/media/rank-finder-landing-background.3a589695.png);
  background-position: top;
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-size: cover;
  overflow: hidden;
}

.masthead h1 {
  color: #1c3e59 !important;
}

header.masthead h1 {
  font-size: 2.25rem;
}

.intro-paragraph {
  max-width: 450px;
}

form > div {
  width: 80% !important;
}

/* CSS Grid for hero image */
.header-image-container {
  display: grid;
  grid-template-columns: auto auto auto;
  width: 100%;
}
.header-image-container > div {
  text-align: center;
  padding: 20px 0;
  font-size: 30px;
}

.image-2 {
  grid-column: 2 / span 1;
  grid-row: 1 / span 2;
}
.image-1 img {
  width: 100%;
}
.image-1 {
  grid-column: 1 / span 3;
  grid-row: 2 / span 3;
}
.image-2 img {
  width: 100%;
}

/* End hero style */

/* Custom Styles */

.custom-section {
  height: 720px;
}

.custom-section p {
  max-width: 450px;
}

.custom-section:nth-of-type(1) {
  background: #ffffff;
}
.custom-section:nth-of-type(2) {
  background: #f0f0f0;
}

/* Back to top button */
.top-link {
  -webkit-transition: all 0.25s ease-in-out;
  transition: all 0.25s ease-in-out;
  position: fixed;
  bottom: 0;
  right: 0;
  display: inline-flex;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  margin: 0 3em 3em 0;
  border-radius: 50%;
  padding: 0.25em;
  width: 50px;
  height: 50px;
  background-color: rgba(70, 143, 201, 0.3);
}
.top-link.show {
  visibility: visible;
  opacity: 1;
}
.top-link.hide {
  visibility: hidden;
  opacity: 0;
}
.top-link svg {
  fill: rgba(28, 62, 89, 0.5);
  width: 24px;
  height: 12px;
}
.top-link:hover {
  background-color: rgba(70, 143, 201, 1);
}
.top-link:hover svg {
  fill: rgba(28, 62, 89, 1);
}

.screen-reader-text {
  position: absolute;
  -webkit-clip-path: inset(50%);
          clip-path: inset(50%);
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  overflow: hidden;
  word-wrap: normal !important;
  clip: rect(1px, 1px, 1px, 1px);
}
.screen-reader-text:focus {
  display: block;
  top: 5px;
  left: 5px;
  z-index: 100000;
  -webkit-clip-path: none;
          clip-path: none;
  background-color: #eee;
  padding: 15px 23px 14px;
  width: auto;
  height: auto;
  text-decoration: none;
  line-height: normal;
  color: #444;
  font-size: 1em;
  clip: auto !important;
}
/* End Back to top button */

/* Section image styles */
.custom-section img {
  width: 80%;
}

/* News and Tips Sections*/
.tips-image-show-sm {
  display: none;
}

.custom-section .indent-box {
  margin-left: 15px;
}

/* Disqus Section */
#disqus_thread {
  margin-top: -150px;
}
@media (min-width: 992px) {
  #mainNav {
    box-shadow: none;
    background-color: transparent;
  }
  #mainNav .navbar-brand {
    color: rgba(255, 255, 255, 0.7);
  }
  #mainNav .navbar-brand:hover {
    color: #fff;
  }
  #mainNav .navbar-nav .nav-item .nav-link {
    color: rgba(255, 255, 255, 0.7);
    padding: 0 1rem;
  }
  #mainNav .navbar-nav .nav-item .nav-link:hover {
    color: #fff;
  }
  #mainNav .navbar-nav .nav-item:last-child .nav-link {
    padding-right: 0;
  }
  #mainNav.navbar-scrolled {
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
    background-color: #fff;
  }
  #mainNav.navbar-scrolled .navbar-brand {
    color: #ffffff;
  }
  #mainNav.navbar-scrolled .navbar-brand:hover {
    color: #f4623a;
  }
  #mainNav.navbar-scrolled .navbar-nav .nav-item .nav-link {
    color: #ffffff;
  }
  #mainNav.navbar-scrolled .navbar-nav .nav-item .nav-link:hover {
    color: #f4623a;
  }

  /* Hero Responsive */
  header.masthead {
    height: 100vh;
    min-height: 40rem;
    padding-top: 72px;
    padding-bottom: 0;
  }
  header.masthead p {
    font-size: 1.15rem;
  }
  header.masthead h1 {
    font-size: 3rem;
  }
  /* End Hero Responsive */
}

@media (min-width: 1200px) {
  header.masthead h1 {
    font-size: 3.5rem;
  }
}

/* Custom responsive adjustments */
@media screen and (max-width: 992px) {
  /* Hero Sections*/
  header.masthead {
    padding-top: 10rem;
    padding-bottom: calc(10rem - 72px);
    background: #f0f0f0;
