    background-size: cover;
    text-align: center;
  }
  header > div > div > div > p {
    margin: 0 auto;
  }
  .bg-dar {
    background: #f0f0f0 !important;
  }
  .bg-dar.scrolled {
    background: #f0f0f0 !important;
    -webkit-animation-name: fade;
            animation-name: fade;
    -webkit-animation-duration: 2.5s;
            animation-duration: 2.5s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
  }
  form > div {
    /* margin: 0 auto !important; */
    width: 100% !important;
  }
  .header-image-container {
    display: none;
  }
  /* End Hero Sections*/

  /* News and Tips Sections*/
  /* This is so that image is tips section moves at the bottom of the text and buttons */
  .custom-section {
    height: 500px;
  }

  .tips-image-show-lg {
    display: none;
  }
  .tips-image-show-sm {
    display: none;
  }

  .custom-section img {
    width: 50%;
  }
  .custom-section:nth-of-type(1) img {
    display: none;
  }
  .custom-section:nth-of-type(2) {
    background: #f0f0f0;
  }

  .custom-section > div > div {
    text-align: center;
  }
  .custom-section h4 {
    margin: 0 auto;
  }
  .custom-section p {
    margin: 20px auto;
    padding-left: 0;
  }
  .custom-section .custom-button-all {
    margin: 0 auto;
  }
  .custom-section .indent-box {
    margin-bottom: 15px;
  }
  /* End News and Tips Sections*/

  /* Disqus Section */
  #disqus_thread {
    margin-top: 0px;
  }
}

